import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; // Si estás usando React Router
import '../../assets/css/perfil.css'
const Perfil: React.FC = () => {
    const [usuarioActual, setUsuarioActual] = useState<any>(null);
    const navigate = useNavigate();

    useEffect(() => {
        const usuario = sessionStorage.getItem('usuarioActual');
        if (usuario) {
            setUsuarioActual(JSON.parse(usuario));
        }
    }, []);

    const handleNavigation = (path: string) => {
        navigate(path);
    };

    return (
        <div>
            {/* Barra de navegación y banner */}
            <header className="arriba">
                <div className="barra">
                    <img src="../imagen/logito.png" alt="logo uleam" className="logito" />
                    <nav className="logo">
                        <div className="btn-container">
                            <button onClick={() => handleNavigation('/perfil')}>Perfil</button>
                        </div>
                    </nav>
                    <nav className="navegacion">
                        <div className="btn-container">
                            <button onClick={() => handleNavigation('/gestionDocumentos')}>Gestión de documentos</button>
                            <button onClick={() => handleNavigation('/verDocumentos')}>Buscar documentos</button>
                        </div>
                    </nav>
                    <nav className="iniciarSesion">
                        <div className="btn-container">
                            <button onClick={() => handleNavigation('/')}>Cerrar sesión</button>
                        </div>
                    </nav>
                </div>
            </header>
            <div className="profile-container">
                <h2>Perfil de Usuario</h2>
                <div id="profileInfo">
                    {usuarioActual ? (
                        <div>
                            <p><strong>Nombre de usuario:</strong> {usuarioActual.username}</p>
                            <p><strong>Nombre:</strong> {usuarioActual.nombre}</p>
                            <p><strong>Apellido:</strong> {usuarioActual.apellido}</p>
                            <p><strong>Correo electrónico:</strong> {usuarioActual.correo}</p>
                            <p><strong>Número de celular:</strong> {usuarioActual.celular}</p>
                            <p><strong>Rol:</strong> {usuarioActual.rol}</p>
                            <p><strong>Departamento:</strong> {usuarioActual.departamento}</p>
                        </div>
                    ) : (
                        <p>No se pudo cargar el perfil.</p>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Perfil;
